<template>
  <svg xmlns="http://www.w3.org/2000/svg" class="map-renderer" width="100%" height="100%" @mousemove="onMouseMove" @click="onClick" v-if="map">
    <defs>
      <filter id="drop-shadow">
        <feDropShadow dx="0" dy="6" stdDeviation="2" flood-color="black" flood-opacity="0.3" />
        <feDropShadow dx="0" dy="3" stdDeviation="2" flood-color="black" flood-opacity="0.3" />
      </filter>
    </defs>
    <g id="map" ref="container">
      <rect v-if="showBounds" x="0" y="0" :width="mapWidth" :height="mapHeight" stroke="black" stroke-width="2" fill-opacity="0" />
      <rect x="0" y="0" :width="mapWidth" :height="mapHeight" :fill="map.background"/>
      <g :filter="showShadows ? 'url(#drop-shadow)' : null">
        <g name="outline">
          <g v-for="path in pathSprites" :key="'path-shadow-' + path.id">
            <polyline
                :path-id="path.id"
                :points="path.points"
                stroke="black"
                :stroke-width="path.strokeWidth + 6"
                fill-opacity="0"
              />
              
              <polygon 
                v-if="path.marker"
                :points="path.marker.points"
                stroke="black"
                fill="black"
                :stroke-width="6"
              />
          </g>
          <g v-for="room in roomsSprites" :key="'room-shadow-' + room.id" :transform="'translate(' + room.x + ' ' + room.y + ')'">
            <wrapper-element v-bind="room.shapeProps" fill="black" :stroke="room.selected ? 'white': 'black'" stroke-width="6" />
          </g>
        </g>
        <g>
          <g>
            <g v-for="path in pathSprites" :key="'path-'+path.id">
            <polyline
              :key="'path-'+path.id"
              :path-id="path.id"
              :points="path.points"
              :stroke="path.color"
              :stroke-width="path.strokeWidth"
              fill-opacity="0"/>

            <polygon 
              v-if="path.marker"
              :points="path.marker.points"
              :stroke="path.color"
              :fill="path.color"
            />

            </g>
          </g>
          <g>
            <g v-for="room in roomsSprites" :key="'room-'+room.id" :transform="'translate(' + room.x + ' ' + room.y + ')'">
              <wrapper-element v-bind="room.shapeProps" :fill="room.fillColor" />
              <image v-if="room.icon" :x="-roomIconSize / 2" :y="-roomIconSize / 2" :width="roomIconSize" :height="roomIconSize" :href="room.icon" />
              <text v-if="showIds" dominant-baseline="middle" text-anchor="middle" fill="white" font-size="12">{{room.id}}</text>
            </g>
          </g>
        </g>
      </g>
      <rect
        v-if="showCursor && cursorPosition"
        :x="cursorPosition.x - tileSize / 2"
        :y="cursorPosition.y - tileSize / 2"
        :width="tileSize"
        :height="tileSize"
        stroke="white"
        fill-opacity="0"
        stroke-width="1"
        stroke-dasharray="2 4"
      />
    </g>
  </svg>
</template>
<style scoped>
svg{
  outline-width: 0;
}
</style>
<script>
import WrapperElement from "./WrapperElement";


function rotate(ax, ay, bx, by, rad) {
  var cos = Math.cos(rad),
    sin = Math.sin(rad),
    run = bx - ax,
    rise = by - ay,
    cx = (cos * run) + (sin * rise) + ax,
    cy = (cos * rise) - (sin * run) + ay;
  return {
    x: cx,
    y: cy
  };
}

function getRandomIntInclusive(min, max) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min + 1)) + min;
}

export function makeDefaultShapes() {
  return {
    pentagon: [
      { x: 0, y: -1.2 },
      { x: -1.14, y: -0.37 },
      { x: -0.71, y: 0.97 },
      { x: 0.71, y: 0.97 },
      { x: 1.14, y: -0.37 },
    ],
    hexagon: [
      { x: 0.6, y: -1.04 },
      { x: -0.6, y: -1.04 },
      { x: -1.2, y: 0 },
      { x: -0.6, y: 1.04 },
      { x: 0.6, y: 1.04 },
      { x: 1.2, y: 0 },
    ],
    trapezoid: [
      { x: -1, y: 1 },
      { x: -0.75, y: -1 },
      { x: 0.75, y: -1 },
      { x: 1, y: 1 },
    ],
    hourglass: [
      { x: -1, y: -1 },
      { x: 0, y: -0.75 },
      { x: 1, y: -1 },
      { x: 1, y: 1 },
      { x: 0, y: 0.75 },
      { x: -1, y: 1 },
    ],
    star: [
      { x: -1, y: -1 },
      { x: 0, y: -0.75 },
      { x: 1, y: -1 },
      { x: 0.75, y: 0 },
      { x: 1, y: 1 },
      { x: 0, y: 0.75 },
      { x: -1, y: 1 },
      { x: -0.75, y: 0 },
    ],
    octagon: [
      { x: -1, y: -0.66 },
      { x: -0.66, y: -1 },
      { x: 0.66, y: -1 },
      { x: 1, y: -0.66 },
      { x: 1, y: 0.66 },
      { x: 0.66, y: 1 },
      { x: -0.66, y: 1 },
      { x: -1, y: 0.66 },
    ],
  };
}

export default {
  components: {
    WrapperElement,
  },
  props: {
    map: {
      type: Object,
      default: null,
    },
    showBounds: {
      type: Boolean,
      default: true,
    },
    showCursor: {
      type: Boolean,
      default: false,
    },
    showShadows: {
      type: Boolean,
      default: true,
    },
    shapes: {
      type: Object,
      default: makeDefaultShapes,
    },
    selectedRooms: {
        type: Array,
        default: () => [],
    },
    showIds: {
        type: Boolean,
        default: false,
    }
  },
  data() {
    return {
      tileSize: 96,
      margin: 32,
      roomSize: 70,
      roomIconSize: 60,
      pathSize: 18,

      cursorPosition: null,
    };
  },
  computed: {
    viewBox() {
      return "0 0 " + this.mapWidth + " " + this.mapHeight;
    },
    mapWidth() {
      return this.map.width * this.tileSize  + 2 * this.margin;
    },
    mapHeight() {
      return this.map.height * this.tileSize + 2 * this.margin;
    },

    roomsById() {
      var output = {};
      for (var room of this.map.rooms) {
        if (output.hasOwnProperty(room.id)) {
          console.error("Duplicate room id: " + room.id);
          continue;
        }
        output[room.id] = room;
      }
      return output;
    },
    roomsSprites() {
      var output = [];
      for (var room of this.map.rooms) {
        var centerX = this.gridCoordinatesToScreenCoordinates(room.x);
        var centerY = this.gridCoordinatesToScreenCoordinates(room.y);

        room.centerX = centerX;
        room.centerY = centerY;

        var sprite = {
          id: room.id,
          x: centerX,
          y: centerY,
          selected: this.selectedRooms.indexOf(room.id) != -1,
          shapeProps: this.resolveShape(room.shape, 0, 0, this.roomSize),
          fillColor: room.color,
          icon: room.icon,
        };
        output.push(sprite);
      }

      return output;
    },
    pathSprites() {
      var output = [];

      for (var path of this.map.paths) {
        if(path.waypoints.length <= 1) continue;

        var coordinates = path.waypoints.map(item => {
          return {
            x: this.gridCoordinatesToScreenCoordinates(item.x),
            y: this.gridCoordinatesToScreenCoordinates(item.y)
          }
        });

        var marker = null;
        if(path.oneWay){

          var last1 = coordinates[coordinates.length -2];
          var last2 = coordinates[coordinates.length -1];

          var angle = Math.atan2(-last2.y + last1.y, last2.x - last1.x);

          var cooeficient = 2.5;

          var markerPosition = {
            x: last1.x + (last2.x - last1.x)/cooeficient,
            y: last1.y + (last2.y - last1.y)/cooeficient,
          };

          var angle = Math.atan2(-last2.y + markerPosition.y, last2.x - markerPosition.x);
          var markerPoints = [
            {x: markerPosition.x, y:markerPosition.y-this.pathSize*4/5},
            {x: markerPosition.x, y:markerPosition.y+this.pathSize*4/5},
            {x: markerPosition.x + this.pathSize+2, y:markerPosition.y},
          ];
          markerPoints = markerPoints.map(p => rotate(markerPosition.x, markerPosition.y, p.x, p.y, angle));
          marker = {
            points: this.makePolygon(markerPoints, 1)
          }
          
          last2.x = markerPosition.x;
          last2.y = markerPosition.y;
        }

        var points = this.makePolygon(coordinates, 1);

        var sprite = {
          id: path.id,
          color: path.color,
          points: points,
          strokeWidth: this.pathSize,
          marker: marker,
        };

        output.push(sprite);
      }

      return output;
    },
  },
  methods: {
    gridCoordinatesToScreenCoordinates(v) {
      return v * this.tileSize + this.tileSize / 2 + this.margin;
    },
    screenCoordinatesToGridCoordinates(v) {
      return (v - this.margin) / this.tileSize;
    },
    clientCoordinatesToGridCoordinates(x, y) {
      var svg = this.$el;
      var container = this.$refs.container;

      var pt = svg.createSVGPoint();
      pt.x = x;
      pt.y = y;

      var coords = pt.matrixTransform(svg.getScreenCTM().inverse());
      coords = coords.matrixTransform(container.getCTM().inverse());

      var ix = this.screenCoordinatesToGridCoordinates(coords.x);
      var iy = this.screenCoordinatesToGridCoordinates(coords.y);

      ix = Math.floor(ix);
      iy = Math.floor(iy);
      return { x: ix, y: iy };
    },

    makePolygon(points, scale) {
      var output = "";
      for (var point of points) {
        output += point.x * scale + ", " + point.y * scale + " ";
      }
      return output;
    },
    resolveShape(shape, x, y, size) {
      var polygon = this.shapes[shape];
      if (polygon != undefined) {
        var polygonString = this.makePolygon(polygon, size / 2);
        return {
          tag: "polygon",
          transform: "translate(" + x + " " + y + ")",
          points: polygonString,
        };
      } else if (shape == "circle") {
        return {
          tag: "circle",
          cx: x,
          cy: y,
          r: size / 2,
        };
      } else if (shape == "rectangle-h") {
        return {
          tag: "rect",
          x: x - (size * 0.7) / 2,
          y: y - size / 2,
          width: size * 0.7,
          height: size,
          rx: 2,
        };
      } else if (shape == "rectangle-v") {
        return {
          tag: "rect",
          x: x - size / 2,
          y: y - (size * 0.7) / 2,
          width: size,
          height: size * 0.7,
          rx: 2,
        };
      } else {
        return {
          tag: "rect",
          x: x - size / 2,
          y: y - size / 2,
          width: size,
          height: size,
          rx: 4,
        };
      }
    },

    onClick(evt) {
      var coords = this.clientCoordinatesToGridCoordinates(evt.clientX, evt.clientY);
      this.$emit("cellclick", { x: coords.x, y: coords.y, shiftKey: evt.shiftKey });
    },
    onRoomClick(room){
        console.log('roomclick'+path.id);
    },
    onPathClick(path){
        console.log('pathclick'+path.id);
    },
    onMouseMove(evt) {
      if (!this.showCursor || !this.map) return;

      var coords = this.clientCoordinatesToGridCoordinates(evt.clientX, evt.clientY);

      // coords.x = Math.max(0, Math.min(coords.x, this.map.width - 1));
      // coords.y = Math.max(0, Math.min(coords.y, this.map.height - 1));

      coords.x = this.gridCoordinatesToScreenCoordinates(coords.x);
      coords.y = this.gridCoordinatesToScreenCoordinates(coords.y);

      this.cursorPosition = coords;
    },
  },
};
</script>
