export default class UndoManager {
  constructor() {
    this.states = [];
    this.currentState = 0;
  }

  push(state) {
      var copy = JSON.stringify(state);
    if(this.currentState == 0 && this.states.length == 0 || this.currentState == this.states.length -1 ){
        this.states.push(copy);
        this.currentState = this.states.length - 1;
    }else{
        this.states.splice(this.currentState + 1, this.states.length - this.currentState - 1, copy);
        this.currentState += 1;
    }
  }

  clear(){
    this.states.splice(0, this.states.length);
    this.currentState = 0;
  }

  hasUndo() {
    return this.currentState > 0;
  }

  hasRedo() {
    return this.currentState < this.states.length - 1;
  }

  undo(callback) {
    if (this.currentState === 0) {
      console.warn("Nothing more undo");
      return;
    }

    this.currentState -= 1;
    var state = JSON.parse(this.states[this.currentState]);
    callback(state);
  }

  redo(callback) {
    if (this.currentState === this.states.length - 1) {
      console.error("Nothing more to redo");
      return;
    }

    this.currentState += 1;

    var state = JSON.parse(this.states[this.currentState]);
    callback(state);
  }
}
