<template>
  <pan-zoom selector="#map" :options="options" @init="onInit" @zoom="onZoom" class="map-preview" v-if="map">
    <map-renderer
      ref="canvas"
      :show-cursor="showCursor"
      :show-bounds="showBounds"
      :map="map"
      :selected-rooms="selectedRooms"
      :show-ids="showIds"
      @cellclick="onCellClick"
    />
    <div class="toolbar">
      <button class="btn btn-primary tool" type="button" @click="zoom(-1)"><i class="las la-minus fa-fw"></i></button>
      <button class="btn btn-primary tool" type="button" @click="resetZoom()"><i class="las la-crosshairs fa-fw"></i></button>
      <button class="btn btn-primary tool" type="button" @click="zoom(1)"><i class="las la-plus fa-fw"></i></button>
    </div>
    <div class="info" v-if="showDebug">
      Rooms: {{ map.rooms.length }}
      <br />
      Paths: {{ map.paths.length }}
    </div>
  </pan-zoom>
</template>
<style scoped>
.map-preview{
  position: relative;
  overflow: hidden;
}
.info {
  position: absolute;
  right: 0.25rem;
  bottom: 0.25rem;
  text-align: right;
  font-size: 80%;
}

.fa-fw {
  text-align: center;
  width: 1.25em;
}

.toolbar {
  position: absolute;
  left: 0.25rem;
  bottom: 0.25rem;
}
.toolbar button:not(:last-child) {
  margin-right: 0.25rem;
}
.toolbar .tool {
  padding: 0.2rem 0.4rem;
  text-align: center;
  font-size: 150%;
}
.btn-primary {
  color: white;
  background-color: #475764;
  border-color: #000000;
}
</style>
<style>
.vue-pan-zoom-item,
.vue-pan-zoom-scene {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}
</style>
<script>
import MapRenderer from "./MapRenderer";

export default {
  components: {
    MapRenderer,
  },
  props: {
    map: {
      default: null,
    },
    selectedRooms: {
      type: Array,
      default: () => [],
    },
    showIds: {
      type: Boolean,
      default: false,
    },
    showCursor: {
      type: Boolean,
      default: true,
    },
    showBounds: {
      type: Boolean,
      default: true,
    },
    showDebug: {
      type: Boolean,
      default: false,
    },
    minZoom: {
      type: Number,
      default: 0.25,
    },
    maxZoom: {
      type: Number,
      default: 1,
    },

    initialZoom: {
      type: Number,
      default: 1,
    },
    zoomStep: {
      type: Number,
      default: 0.1,
    },
  },
  data() {
    return {
      zoomer: null,
      initialTransform: null,
    };
  },
  computed: {
    options() {
      return {
        bounds: true,
        boundsPadding: 0.5,
        zoomDoubleClickSpeed: 1.5,
        maxZoom: this.maxZoom,
        minZoom: this.minZoom,
      };
    },
  },
  methods: {
    onInit(zoomer) {
      this.zoomer = zoomer;
      this.initialTransform = { ...zoomer.getTransform() };
      this.initialTransform.scale = 1;
      this.zoomer.zoomAbs(this.initialTransform.x, this.initialTransform.y, this.initialTransform.scale);
    },
    onZoom() {
    },
    onCellClick(evt) {
      this.$emit("cellclick", evt);
    },
    zoom(direction) {
      if (this.zoomer == null) return;
      var t = this.zoomer.getTransform();
      this.zoomer.smoothZoomAbs(t.x, t.y, t.scale + direction * this.zoomStep);
    },
    resetZoom() {
      if (this.zoomer == null) return;
      this.zoomer.smoothZoomAbs(this.initialTransform.x, this.initialTransform.y, this.initialTransform.scale);
    },
  },
};
</script>
